* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body{
  height: 100%;
}

#root {
  display: flex;
  min-height: 100%;
  flex-direction: column;
  align-items: center;
}

.App {
  width: 800px;
}

.login-page{
  margin: auto;
}
.login-page p {
  color: red;
}

.post {
  display: flex;
  padding: 15px;
  border: 2px solid teal;
  margin-top: 15px;
  justify-content: space-between;
  align-items: center;
}
.post__content{
  flex: 0 1 75%;
}
.post__content span{
  display: inline-block;
}
.post__content span::first-letter{
  text-transform: uppercase;
}

.post__btns{
  display: flex;
  justify-content: space-around;
  flex: 0 1 20%;
}

.post__id-page{
  margin-top: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.post__id-page_title{
  color: teal;
  font-weight: bold;
}
.post__id-page_coments{
  margin-top: 30px;
}
.post__id-page_coment{
  width: 70%;
  margin: 0 auto;
}

.about-app{
  margin: auto;
}
.about-app ul {
  margin-top: 30px;
  font-size: 18px;
}

/* animation */
.post-enter {
  transform: translate(-350px);
}
.post-enter-active {
  transform: translate(0px);
  transition: all 500ms ease-in;
}
.post-exit {
  opacity: 1;
}
.post-exit-active {
  transform: translateX(-350px);
  transition: all 500ms ease-in;
}
.post__last-element{
  height: 20px;
  background: transparent;
}
.page__wrapper{
  margin: 30px 0;
  display: flex;
  justify-content: center;
}

.page {
  padding: 10px;
  border: 1px solid teal;
  cursor: pointer;
}

.page__current{
  border: 2px solid orange;
  font-weight: bold;
}

.navbar{
  height: 50px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 15px;
  background: lightgray;
}

.navbar__link.active{
  color: white;
  background: teal;
}

.navbar__link{
  padding: 8px;
  text-decoration: none;
  color: teal;
  border-radius: 10px;
}
.footer{
  margin-top: auto;
  height: 70px;
  background: teal;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer p {
  color: lightgray;
  font-weight: bold;
}